.loader,.loader:before,.loader:after{
  top:0;
  bottom:0;
  left:0;
  right:0; 
  content:"";
  position:absolute;
  border-radius:50%;
}

.loader{
  position:fixed;
  width:100px;
  height:100px;
  margin:auto;
  -webkit-animation:spin 4s linear infinite;
          animation:spin 4s linear infinite;
}

@-webkit-keyframes spin{
  100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}
}

@keyframes spin{
  100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}
}

.loader:before{
  border:5px solid rgb(216, 213, 213);
  border-bottom:5px solid #ffe521;
  border-left:5px solid #ffe521;
  -webkit-animation:spin1 2s linear infinite;
          animation:spin1 2s linear infinite;
}

.loader:after{
  border:5px solid rgb(216, 213, 213);
  border-top:5px solid transparent;
  border-right:5px solid transparent;
  -webkit-animation:spin2 2s linear infinite;
          animation:spin2 2s linear infinite;
}

@-webkit-keyframes spin1{
  20%{-webkit-transform:rotate(150deg);transform:rotate(150deg)}
  40%{-webkit-transform:rotate(300deg);transform:rotate(300deg)}
  80%{-webkit-transform:rotate(300deg);transform:rotate(300deg)}
  100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}
}

@keyframes spin1{
  20%{-webkit-transform:rotate(150deg);transform:rotate(150deg)}
  40%{-webkit-transform:rotate(300deg);transform:rotate(300deg)}
  80%{-webkit-transform:rotate(300deg);transform:rotate(300deg)}
  100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}
}

@-webkit-keyframes spin2{
  0%{-webkit-transform:rotate(-30deg);transform:rotate(-30deg)}
  20%{-webkit-transform:rotate(-30deg);transform:rotate(-30deg);
      border-color:transparent transparent rgb(216, 213, 213) rgb(216, 213, 213)}
  21%{border-color:#ffe521 #ffe521 transparent transparent}
  40%{-webkit-transform:rotate(-30deg);transform:rotate(-30deg)}
  60%{-webkit-transform:rotate(120deg);transform:rotate(120deg);
      border-color:#ffe521 #ffe521 transparent transparent}
  61%{border-color:transparent transparent rgb(216, 213, 213) rgb(216, 213, 213)}
  80%{-webkit-transform:rotate(270deg);transform:rotate(270deg)}
  100%{-webkit-transform:rotate(330deg);transform:rotate(330deg);}
}

@keyframes spin2{
  0%{-webkit-transform:rotate(-30deg);transform:rotate(-30deg)}
  20%{-webkit-transform:rotate(-30deg);transform:rotate(-30deg);
      border-color:transparent transparent rgb(216, 213, 213) rgb(216, 213, 213)}
  21%{border-color:#ffe521 #ffe521 transparent transparent}
  40%{-webkit-transform:rotate(-30deg);transform:rotate(-30deg)}
  60%{-webkit-transform:rotate(120deg);transform:rotate(120deg);
      border-color:#ffe521 #ffe521 transparent transparent}
  61%{border-color:transparent transparent rgb(216, 213, 213) rgb(216, 213, 213)}
  80%{-webkit-transform:rotate(270deg);transform:rotate(270deg)}
  100%{-webkit-transform:rotate(330deg);transform:rotate(330deg);}
}


